import classes from './styles.module.scss'
import React, {useState} from 'react';
import {Helmet} from "react-helmet";

//  
const FComponent = ({ gods, addGoodsBasket }) => {
//gods - ,   
//addGoodsBasket - ,     

//  ,      
const sucsesElement = <div className={classes.sucses} >     </div>

// ,  ,     
let [showSucses, setshowSucses] = useState(false)

//,     
const pressButton = () => {
	setshowSucses(true)
	addGoodsBasket(gods)
	setTimeout(() =>{
		setshowSucses(false)
	}, 1000)
}
	
return <div className={classes.wrapper}>

	<Helmet>

		<title> { gods?.description || ', '} </title>
		<meta name="description" content="
			  ,   , 
			  , , , , ,
			, .
		"/>
	</Helmet>
	
	{ !showSucses || sucsesElement }

	<h1> {gods.description} </h1>
	
	{gods.video?<video src={gods.video} controls={true} preload={'metadata'}/>
	:<img src={gods.img} alt={gods.name} />}
	
	<h3> : {gods.price}  </h3>
	
	<button onClick={pressButton}>    </button>
	
</div>
}

export default FComponent
