// @ts-ignore
import classes from './Style.module.scss'
import React from 'react';

const OrderComplete = ({ setactiveComponent, sendMail, data, gods }) => {

	// ,  1  
	const goStart = () => setactiveComponent(0)
	
	//,   
	const goBack = () => setactiveComponent(1)
	
	//,    
	const sendMails = () => sendMail()
	
	//,   
	const setGodsList = () => {
		let totalPrice = 0
		let godList = []

		for(let key in gods){
			totalPrice += gods[key].price*gods[key].count
			godList.push(
				<div className={classes.label} key={gods[key].name}>
					<span> 
						: {gods[key].description||''}, 
						{gods[key].count}   
						{gods[key].price||''} 
					</span>
					<img src={gods[key].img} />
				</div>
			)
		}
		godList.push(<div className={classes.label} key={0}>
		 : {totalPrice} 
		</div>)
		return godList
	}

	return <div className={classes.contactData}>
		<h4>   </h4>
		<div className={classes.form}>

			<div className={classes.formField}>
				<div className={classes.label}>": {data.firstName||' '}</div>
				<div className={classes.label}>: {data.lastName||' '}</div>
				<div className={classes.label}>: {data.tel||' '}</div>
				<div className={classes.label}> : {data.namePost||' '}</div>
				<div className={classes.label}>: {data.departCurent||' '}</div>
				{setGodsList()}
			</div>

			<div className={classes.formField}>
				<button onClick={sendMails}> </button>
				<button onClick={goBack}></button>
				<button onClick={goStart}> </button>
			</div>
		</div>
	</div>
}

export default OrderComplete